.nav-main
    +sm-block
        display: none
    ul
        display: flex
        align-items: center
        justify-content: space-between
    a
        font-size: 18px
        font-weight: 700
        text-decoration: none
        color: #000
        text-transform: uppercase
        &:hover
            color: $blue-color
        &.active
            color: $blue-color

.nav-mobile
    a
        display: block
        font-size: 20px
        font-weight: 600
        margin-bottom: 20px
        text-decoration: none
        color: #fff !important
        &:hover
            opacity: .7
        &:last-child
            margin-bottom: 0