.plug
    &__wrapper 
        display: flex
        flex-direction: column
        justify-content: center
        align-items: center
        min-height: 100vh
        text-align: center
    &__img
        display: block
        margin: 0 auto
        overflow: hidden
        max-width: 251px
        min-width: 251px
        height: 107px
        img
            display: block
            width: 100%
            height: 100%
            object-fit: contain

        @media screen and (max-width: 767px) 
            max-width: 140px
            min-width: 140px
            height: 60px
    &__img + &__body,
    &__body + &__img
        margin-top: 62px
    &__title 
        margin: 0
        font-family: $base-font
        font-size: 20px
        font-style: normal
        font-weight: bold
        text-transform: none
        color: #000000
        
        @media screen and (max-width: 767px) 
            font-size: 16px
    &__description
        margin: 10px 0 0 0
        font-family: $base-font
        font-size: 14px
        font-weight: normal
        color: #000000
    &__btn
        margin: 58px auto 0
        min-width: 199px